.calculator {
  background-color: grey;
  display: inline-block;
  padding: 20px;
  border-radius: 20px;
  text-align: center;
  font-weight: bolder;
  border: solid;
  width: 500px;
  margin: 5% 0 0 30%;
  box-shadow: black;
}
.button {
  width: 80px;
  height: 80px;
  margin: 3px;
  background-color: white;
  font-weight: bold;
}
.operator {
  background-color: #ff8800;
}
.clear {
  background-color: #cc0000;
}
.screen {
  background-color: whitesmoke;
  width: 400px;
  height: 30px;
  border: none;
  font-size: 20px;
  text-align: right;
}
@media only screen and (max-width: 600px) {
  .calculator {
    background-color: grey;
    display: inline-block;
    padding: 20px;
    border-radius: 20px;
    text-align: center;
    font-weight: bolder;
    border: solid;
    width: 350px;
    margin: 8% 0 0 1%;
  }
  .button {
    width: 60px;
    height: 60px;
    margin: 2px;
    background-color: white;
    font-weight: bold;
  }
  .operator {
    background-color: #ff8800;
  }
  .clear {
    background-color: #cc0000;
  }
  .screen {
    background-color: whitesmoke;
    width: 300px;
    height: 30px;
    border: none;
    float: right;
    text-align: right;
  }
}
